@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*, *::before, *::after {
  box-sizing: border-box; }

img {
  vertical-align: middle;
  max-width: 100%;
  height: auto; }

body {
  font-family: Roboto,sans-serif,serif;
  font-style: italic;
  font-size: 24px;
  font-weight: 500; }

a {
  text-decoration: none;
  color: #fff; }

.container {
  max-width: 1200px;
  margin: 0 auto; }

.header, .bg-style {
  color: #fff; }

.bg-style {
  background: #000; }

.header p {
  line-height: 42px;
  font-weight: 700; }

h1 {
  font-size: 80px;
  line-height: 106px;
  font-weight: 900; }

.title {
  margin: 0 0 32px 0; }

.subTitle {
  max-width: 283px; }

.subTitle p {
  line-height: 36px;
  text-align: justify;
  margin: 0 0 40px 0; }

.subTitle ul {
  display: flex;
  justify-content: space-between; }

.subTitle li a:hover {
  opacity: 0.6; }

.header {
  background-image: url(../images/banner.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 958px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.profile, .bg-style, .works {
  text-align: center; }

h2 {
  font-size: 64px;
  line-height: 84px; }

.profile {
  margin: 56px auto; }

.profile img {
  margin: 40px 0 0 0; }

.profile li {
  line-height: 42px;
  font-weight: 700; }

.profile img + ul {
  padding: 20px 0; }

.profile ul li + li {
  padding: 4px 0; }

.workEXP {
  max-width: 384px;
  border-top: 1px solid #000;
  margin: 0 auto; }

.dot::before {
  content: "";
  width: 22px;
  height: 22px;
  background: #891818;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  margin: 0 16px 0 0;
  /*偽元素本身是行內元素*/
  display: inline-block; }

.workEXP li {
  margin: 28px 0 0 0; }

.brand h2 {
  padding: 56px 0 0 0;
  margin: 0 0 12px 0; }

.brand h3 {
  line-height: 42px;
  margin: 0 0 32px 0; }

.brand p {
  font-size: 20px;
  line-height: 30px;
  margin: 0 0 68px 0; }

.impression {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 0 160px 0; }

.brand01 {
  margin: 0 auto 40px; }

.works {
  margin: 56px auto 144px; }

.works p {
  font-size: 20px;
  line-height: 30px; }

.works > p {
  margin: 16px 0 56px 0; }

.works li p {
  line-height: 42px;
  margin: 16px 0 48px 0; }

.works ul {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

.footer {
  padding: 64px 0; }

.footer p {
  line-height: 36px;
  margin: 16px 0; }

.footer a:hover {
  text-decoration: underline; }
